export const HeaderController = () => {
  // Reads out the scroll position and stores it in the data attribute
  // so we can use it in our stylesheets
  const storeScroll = () => {
    if (window.scrollY > 0) {
      $('html').addClass('scroll')
    } else {
      $('html').removeClass('scroll')
    }

    const scrollPosition = window.scrollY;
    const windowHeight = document.body.scrollHeight;
    const innerHeight = window.innerHeight;
    const scrollHeight = (windowHeight - innerHeight);
    // const scrollBarHeight = 100 - (scrollHeight / windowHeight * 100)

    const scrollPercentage = ((100 / scrollHeight) * (scrollPosition / 100) * 100);
    // $('.scrollbar').css('top', `${scrollPercentage}%`);

    $('.scrollbar').css('height', `${scrollPercentage}%`);
  };

  // Listen for new scroll events
  document.addEventListener("scroll", storeScroll);

  // Update scroll position for first time
  storeScroll();


  $('.nav-main-mobile .hamburger-icon').on('click', () => {
    $('.header').toggleClass('open');
  })
}



